#toastId {
  position: fixed;
  top: 32px;
  right: 32px;
  z-index: 1000;
}
.toast {
  display: flex;
  align-items: center;
  background-color: #fff;
  border-radius: 2px;
  padding: 20px 0;
  min-width: 400px;
  max-width: 450px;
  border-left: 4px solid;
  box-shadow: 0 5px 8px rgba(0, 0, 0, 0.08);
  transition: all ease 0.3s;
  animation: slideInLeft 0.4s;
  &:hover {
    background-color: rgb(234, 233, 233);
  }
  .toast__icon {
    font-size: 24px;
  }
  .toast__icon,
  .toast__close {
    padding: 0 16px;
  }
  .toast__body {
    flex-grow: 1;
  }
  .toast__title {
    font-size: 16px;
    font-weight: 600;
    color: #333;
  }
  .toast__message {
    font-size: 16px;
    color: #333;
    margin-top: 4px;
  }
  .toast__close {
    font-size: 20px;
    color: rgba(0, 0, 0, 0.3);
    cursor: pointer;
  }
  @keyframes slideInLeft {
    from {
      opacity: 0;
      transform: translateX(calc(100% + 32px));
    }
    to {
      opacity: 1;
      transform: translateX(0);
    }
  }

  @keyframes fadeOut {
    to {
      opacity: 0;
    }
  }
}
.toast + .toast {
  margin-top: 24px;
}
.toast--success {
  border-color: #47d864;
  transition: 0.3s ease;
}
.toast--success .toast__icon {
  color: #47d864;
}

.toast--remove {
  border-color: #ff623d;
}
.toast--remove .toast__icon {
  color: #ff623d;
}
.toast-wrapper + .toast-wrapper {
  margin-top: 24px;
}
